.wp-block-blocks-gamestore-block-cta {
  position: relative;
  background-size: cover;
  margin-top: clamp(3.75rem, calc(2.77rem + 4.92vw), 7.5rem)!important;
  margin-bottom: clamp(3.75rem, calc(2.77rem + 4.92vw), 7.5rem)!important;
  padding-top: clamp(3.75rem, calc(2.77rem + 4.92vw), 7.5rem)!important;
  padding-bottom: clamp(3.75rem, calc(2.77rem + 4.92vw), 7.5rem)!important;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, var(--background-default) 0%, var(--background-80) 50.06%, var(--background-default) 100%);
    z-index: 0;
  }

  .cta-inner {
    border-radius: 8px;
    border: 1px solid var(--divider-main, rgba(14, 13, 15, 0.08));
    background: var(--background-2, rgba(250, 250, 250, 0.02));
    backdrop-filter: blur(12px);
    display: flex;
    align-items: stretch;
    gap: 30px;
    position: relative;

    .right-part {

      .image-cta {
        position: absolute;
        bottom: 0;
        right: -50px;
        max-height: 487px;
      }
    }

    .left-part {
      display: flex;
      flex-direction: column;
      gap: 16px;
      padding: 48px;
      max-width: 930px;
      width: 60%;

      .cta-title {
        margin: 0;
      }

      .cta-description {
        margin: 0;
        color: var(--text-secondary, rgba(14, 13, 15, 0.70));
      }

      .links-list {
        margin-top: 16px;
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        gap: 24px;

        a {
          border-radius: 5px;
          border: 1px solid var(--divider-main, rgba(14, 13, 15, 0.08));
          padding: 20px 32px;
          display: inline-flex;
          color: var(--text-primary, #0E0D0F);
          font-size: 14px;
          font-weight: 700;
          line-height: 140%;
          letter-spacing: 2.1px;
          text-transform: uppercase;
          text-decoration: none;
        }
      }
    }
  }
}